.section {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border: 1px solid #87888b;
  border-radius: 4px;
  padding: 50px;
  box-shadow: 0 10px 10px rgba(0, 0, 0, 0.1);
  max-width: 500px;
  margin: 0 auto;
}

.title {
  margin-bottom: 20px;
  margin-top: 15px;
  font-size: 25px;
  text-align: center;
  text-transform: uppercase;
}
